import React, { Component } from "react";
import { Form, Input, Button, Select } from "antd";
import E from "wangeditor";
import { editSubmission, examSubject, examType, TopicTypes } from "@/api/watch";
// import "@/assets/css/addQuestions.css";

const { Option } = Select;

class delate extends Component {
  constructor(props) {
    super(props);
    this.state = {
      editorContent: "",
      Answer: "",
      data: "",
      subjectData: "",
      examType: "",
      TopicTypes: ""
    };
  }
  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        let { id } = this.props.match.params;
        let questions_answer = this.state.editorContent;
        let { title, exam_type, course_type, topic_type } = values;
        //
        editSubmission({
          questions_answer,
          title,
          exam_type,
          subject_text: course_type,
          questions_type_text: topic_type,
          questions_id: id
        }).then(res => {
          console.log(res);
        });
      }
    });
  };
  render() {
    const { subjectData, examType, TopicTypes } = this.state;
    const {
      title,
      questions_answer,
      questions_theme
    } = this.props.location.item;
    const { getFieldDecorator } = this.props.form;

    return (
      <div className="addQuestions">
        <div style={{ overflow: "auto" }}>
          <Form
            onSubmit={this.handleSubmit}
            className="login-form"
            style={{ margin: "30px" }}
          >
            <Form.Item>
              <div className="information">
                <h3></h3>
                <p></p>
                {getFieldDecorator("title", {
                  rules: [
                    { required: true, message: "Please input your title!" }
                  ]
                })(
                  <Input
                    placeholder={title}
                    style={{ width: "490px", height: "40px" }}
                  />
                )}
              </div>
            </Form.Item>
            <Form.Item>
              <div className="shop">
                <h4></h4>
                <div className="text-area">
                  <div
                    ref="editorElemMenu"
                    style={{
                      backgroundColor: "#f1f1f1",
                      border: "1px solid #ccc"
                    }}
                    className="editorElem-menu"
                  ></div>
                  <div
                    style={{
                      padding: "0 10px",
                      overflowY: "scroll",
                      height: 300,
                      border: "1px solid #ccc",
                      borderTop: "none"
                    }}
                    ref="editorElemBody"
                    className="editorElem-body"
                  >
                    <div
                      dangerouslySetInnerHTML={{ __html: questions_theme }}
                    />
                  </div>
                </div>
              </div>
            </Form.Item>
            <Form.Item>
              <div className="examination">
                <p>:</p>
                {getFieldDecorator("exam_type", {
                  rules: [
                    { required: true, message: "Please input your exam_type!" }
                  ]
                })(
                  <Select
                    placeholder=":"
                    style={{ width: "32%", height: "50" }}
                  >
                    {examType &&
                      examType.map((item, index) => {
                        return (
                          <Option key={item.tid} value={item.type_name}>
                            {item.type_name}
                          </Option>
                        );
                      })}
                  </Select>
                )}
              </div>
              <div className="curriculum">
                <p>:</p>
                {getFieldDecorator("course_type", {
                  rules: [
                    {
                      required: true,
                      message: "Please input your course_type!"
                    }
                  ]
                })(
                  <Select
                    placeholder=":"
                    style={{ width: "32%", height: "50" }}
                  >
                    {subjectData &&
                      subjectData.map((item, index) => {
                        return (
                          <Option
                            key={item.subject_id}
                            value={item.subject_text}
                          >
                            {item.subject_text}
                          </Option>
                        );
                      })}
                  </Select>
                )}
              </div>
              <div className="topic">
                <p>:</p>
                {getFieldDecorator("topic_type", {
                  rules: [
                    { required: true, message: "Please input your topic_type!" }
                  ]
                })(
                  <Select
                    placeholder=":"
                    style={{ width: "32%", height: "50" }}
                  >
                    {TopicTypes &&
                      TopicTypes.map((item, index) => {
                        return (
                          <Option
                            key={item.id}
                            value={item.questions_type_text}
                          >
                            {item.questions_type_text}
                          </Option>
                        );
                      })}
                  </Select>
                )}
              </div>
            </Form.Item>
            <Form.Item>
              <h3></h3>
              <div className="shop">
                <div className="text-area">
                  <div
                    ref="editorElemAnswer"
                    style={{
                      backgroundColor: "#f1f1f1",
                      border: "1px solid #ccc"
                    }}
                    className="editorElem-menu"
                  ></div>
                  <div
                    style={{
                      padding: "0 10px",
                      overflowY: "scroll",
                      height: 300,
                      border: "1px solid #ccc",
                      borderTop: "none"
                    }}
                    ref="editorElemAnswerBody"
                    className="editorElem-body"
                  >
                    <div
                      dangerouslySetInnerHTML={{ __html: questions_answer }}
                    />
                  </div>
                </div>
              </div>
            </Form.Item>
            <Form.Item>
              <Button
                type="primary"
                htmlType="submit"
                className="login-form-button"
              >
                
              </Button>
            </Form.Item>
          </Form>
        </div>
      </div>
    );
  }
  componentDidMount() {
    const elemMenu = this.refs.editorElemMenu;
    const elemBody = this.refs.editorElemBody;
    const elemMenu2 = this.refs.editorElemAnswer;
    const elemAnswerBody = this.refs.editorElemAnswerBody;
    const editor = new E(elemMenu, elemBody);
    const Answer = new E(elemMenu2, elemAnswerBody);

    // (javascript...)
    examSubject().then(res => {
      this.setState({
        subjectData: res.data.result
      });
    });

    //
    examType().then(res => {
      this.setState({
        examType: res.data.result
      });
    });
    //
    TopicTypes().then(res => {
      this.setState({
        TopicTypes: res.data.result
      });
    });

    //  onchange  state 
    editor.customConfig.onchange = html => {
      this.setState({
        editorContent: editor.txt.html()
      });
    };
    Answer.customConfig.onchange = html => {
      this.setState({
        Answer: editor.txt.html()
      });
    };
    Answer.customConfig.menus = [
      "head", // 
      "bold", // 
      "fontSize", // 
      "fontName", // 
      "italic", // 
      "underline", // 
      "strikeThrough", // 
      "foreColor", // 
      "backColor", // 
      "link", // 
      "list", // 
      "justify", // 
      "quote", // 
      "emoticon", // 
      "image", // 
      "table", // 
      "video", // 
      "code", // 
      "undo", // 
      "redo" // 
    ];
    Answer.customConfig.uploadImgShowBase64 = true;
    Answer.create();
    Answer.customConfig.uploadImgServer =
      "https://gtacms.gtarcade.com/backend/Answer/index?action=uploadimage"; // 
    // 3M
    Answer.customConfig.uploadImgMaxSize = 3 * 1024 * 1024;
    //  5 
    Answer.customConfig.uploadImgMaxLength = 1;
    // 
    Answer.customConfig.uploadFileName = "Answer_img";
    //  timeout  3s
    Answer.customConfig.uploadImgTimeout = 5000;
    Answer.customConfig.uploadImgHooks = {
      before: function(xhr, editor, files) {
        // 
        // xhr  XMLHttpRequst editor files 
        //  {prevent: true, msg: 'xxxx'} 
        // return {
        //     prevent: true,
        //     msg: ''
        // }
        // alert("");
      },
      success: function(xhr, editor, result) {
        // 
        // xhr  XMLHttpRequst editor result 
        // var url = result.data.url;
        // alert(JSON.stringify(url));
        // editor.txt.append(url);
        // alert("");
      },
      fail: function(xhr, editor, result) {
        // 
        // xhr  XMLHttpRequst editor result 
        alert("");
      },
      error: function(xhr, editor) {
        // 
        // xhr  XMLHttpRequst editor 
        // alert("");
      },
      //  {errno:0, data: [...]} 
      //  JSON 
      customInsert: function(insertImg, result, editor) {
        // 
        // insertImg editor result 
        //  {url:'....'} 
        var url = result.data[0];
        insertImg(url);
        // result  JSON 
      }
    };

    editor.customConfig.menus = [
      "head", // 
      "bold", // 
      "fontSize", // 
      "fontName", // 
      "italic", // 
      "underline", // 
      "strikeThrough", // 
      "foreColor", // 
      "backColor", // 
      "link", // 
      "list", // 
      "justify", // 
      "quote", // 
      "emoticon", // 
      "image", // 
      "table", // 
      "video", // 
      "code", // 
      "undo", // 
      "redo" // 
    ];
    editor.customConfig.uploadImgShowBase64 = true;
    editor.create();
    editor.customConfig.uploadImgServer =
      "https://gtacms.gtarcade.com/backend/editor/index?action=uploadimage"; // 
    // 3M
    editor.customConfig.uploadImgMaxSize = 3 * 1024 * 1024;
    //  5 
    editor.customConfig.uploadImgMaxLength = 1;
    // 
    editor.customConfig.uploadFileName = "editor_img";
    //  timeout  3s
    editor.customConfig.uploadImgTimeout = 5000;

    editor.customConfig.uploadImgHooks = {
      before: function(xhr, editor, files) {
        // 
        // xhr  XMLHttpRequst editor files 
        //  {prevent: true, msg: 'xxxx'} 
        // return {
        //     prevent: true,
        //     msg: ''
        // }
        // alert("");
      },
      success: function(xhr, editor, result) {
        // 
        // xhr  XMLHttpRequst editor result 
        // var url = result.data.url;
        // alert(JSON.stringify(url));
        // editor.txt.append(url);
        // alert("");
      },
      fail: function(xhr, editor, result) {
        // 
        // xhr  XMLHttpRequst editor result 
        alert("");
      },
      error: function(xhr, editor) {
        // 
        // xhr  XMLHttpRequst editor 
        // alert("");
      },
      //  {errno:0, data: [...]} 
      //  JSON 
      customInsert: function(insertImg, result, editor) {
        // 
        // insertImg editor result 
        //  {url:'....'} 
        var url = result.data[0];
        insertImg(url);
        // result  JSON 
      }
    };
  }
  componentWillUnmount() {
    this.setState = (state, callback) => {
      return;
    };
  }
}

export default Form.create({ name: "normal_login" })(delate);
